@import url('https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap');

.word_cloud {
    font-family: Rubik Mono One, sans-serif;
    display: flex;
    justify-content: center;
}
.word_cloud--item {
    cursor: pointer;
    color: #000;
    font-size: 2rem;
    text-shadow: 0 0 4px rgb(255 255 255 / 80%)

}